char* ComputeFkWrapper(const IkReal* j) {
    IkReal eetrans[3] = {0};
    IkReal eerot[9] = {0};
    char buffer [256] = {0};
    ComputeFk(j, eetrans, eerot);
    for(int i = 0; i < 3; ++i)
        sprintf(buffer + strlen(buffer), "%.15f,", eetrans[i]);
    for(int i = 0; i < 9 - 1; ++i)
        sprintf(buffer + strlen(buffer), "%.15f,", eerot[i]);
    sprintf(buffer + strlen(buffer), "%.15f", eerot[9 - 1]);
    return buffer;
}

